<template>
    <div class="descripcion-content">
        <div class="container">
            <div class="card card-muni">
                <div class="row">
                    <div class="col-md-12">
                        <div class="row">
                            <div class="col">
                                <table class="tabla-reporte">
                                    <tbody is="EstadoCuentaTributoView" v-for="(tributo, i) in tributos" :key="i"
                                    v-bind:tributo="tributo"
                                    v-bind:origen="'ecuentaSeleccionado'">
                                    </tbody>
                                    <tfoot>
                                        <tr>
                                            <th colspan="16">&nbsp;</th>
                                        </tr>
                                        <tr>
                                            <td colspan="16" class="pl-0">
                                                <div class="alert alert-success text-right">
                                                    <h4 class="mb-0">
                                                        Total a pagar:
                                                        <b>S/.{{totalPago | formatoMonedax}}</b>
                                                    </h4>
                                                    
                                                </div>
                                            </td>
                                        </tr>
                                    </tfoot>
                                </table>
                                
                                <hr/>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-xs-12 col-sm-12 col-md-6 text-left d-none d-sm-block">
                                <button class="btn btn-primary" @click="cancelarProceso()">Cancelar proceso</button>
                                <button class="btn btn-success" @click="regresarPagina()">Regresar</button>
                            </div>
                            <div class="col-xs-12 col-sm-12 col-md-6 text-center d-sm-none">
                                <button class="btn btn-primary" @click="cancelarProceso()">Cancelar proceso</button>
                                <button class="btn btn-success" @click="regresarPagina()">Regresar</button>
                            </div>
                            <div class="col-xs-12 col-sm-12 col-md-6">
                                <div class="col-xs-12 text-right d-none d-sm-block">
                                    <button class="btn btn-danger" @click="iniciarPago">PROCESAR PAGO</button>
                                </div>
                                <div class="col-xs-12 text-center d-sm-none">
                                    <button class="btn btn-danger" @click="iniciarPago">PROCESAR PAGO</button>
                                </div>
                            </div>
                        </div>
                        <div class="row pt-3 pb-5">
                            <div class="col text-left d-none d-sm-block">
                                <div>
                                    Nota:<a href="javascript:;" onclick="afiliatevbv()">Si paga con VISA y no esta afiliado a Verified by Visa, su operación, puede salir DENEGADA.</a>
                                </div>
                                <div>
                                    <img src="../../assets/visa.gif" alt="VisanetPeru" width="45" height="28" border="0" align="middle" class="simLink" onclick="visa()">&nbsp;
                                    <img src="../../assets/logo_mc.gif" alt="Proceso MC Peru" width="69" height="22" align="middle" class="simLink" onclick="procesosmc()">&nbsp;
                                    <img src="../../assets/mastercard.gif" alt="MasterCard" width="45" height="29" border="0" align="middle" class="simLink" onclick="master()">&nbsp;
                                    <img src="../../assets/ameexp.jpg" alt="American Express" width="45" height="45" border="0" align="middle" class="simLink" onclick="american()">&nbsp;
                                </div>
                                <div>
                                    Sitio autorizado, para realizar transacciones electrónicas seguras.
                                    <img src="../../assets/candadito.gif" alt="Sitio Seguro">
                                </div>
                            </div>
                            <div class="col text-center d-sm-none">
                                <div>
                                    Nota:<a href="javascript:;" onclick="afiliatevbv()">Si paga con VISA y no esta afiliado a Verified by Visa, su operación, puede salir DENEGADA.</a>
                                </div>
                                <div>
                                    <img src="../../assets/visa.gif" alt="VisanetPeru" width="45" height="28" border="0" align="middle" class="simLink" onclick="submitVisa()">&nbsp;
                                    <img src="../../assets/logo_mc.gif" alt="Proceso MC Peru" width="69" height="22" align="middle" class="simLink" onclick="procesosmc()">&nbsp;
                                    <img src="../../assets/mastercard.gif" alt="MasterCard" width="45" height="29" border="0" align="middle" class="simLink" onclick="master()">&nbsp;
                                    <img src="../../assets/ameexp.jpg" alt="American Express" width="45" height="45" border="0" align="middle" class="simLink" onclick="american()">&nbsp;
                                </div>
                                <div>
                                    Sitio autorizado, para realizar transacciones electrónicas seguras.
                                    <img src="../../assets/candadito.gif" alt="Sitio Seguro">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import EstadoCuentaTributoView from './EstadoCuentaTributo.vue'
import EventBus from '../../helpers/EventBus'

import Util from '../../tools/util'
import UtilNumerico from '../../tools/utilNumerico'
import Constantes from '../../store/constantes'

import {getEstadoCuentaSec} from '../services/rentasService'
import {getDataCuenta} from '../services/rentasService'

export default {
    components:{ EstadoCuentaTributoView },
    data(){
        return {
            app: {
                name: "appRentas", 
                data: null
            },
            estadoCuenta:{},
            tributos:[],
            totalPago:0,
            dataRecibos:[]
        }
    },
    created(){
        this.app.data = Util.storage.get(this.app.name);
        this.iniciaProceso();
        console.log(this.app.data);
    },
    methods:{
        iniciaProceso(){
            EventBus.$emit("loadershow", true);

            getEstadoCuentaSec(this.app.data.cuenta.emision).then((response)=>{
                if(response.data.success){
                    let tributos = [];

                    tributos = response.data.data.tributos;
                    this.estadoCuenta = response.data.data;
                    this.tributos = this.procesarRecibosSeleccionados(tributos);
                }

                EventBus.$emit("loadershow", false);
            }).catch(err=>console.log(err));
        },
        procesarRecibosSeleccionados(tributos){
            let recibosSeleccionados = this.app.data.cuenta.recibossel;
            let glosaAnterior = "";
            let grupoAnterior = false;
            let tributox = [];
            let sumaTotalPagar = 0;

            tributos.forEach(item => {
                let objItem = {};
                let itemsEncontrados = [];
                let cabeceraRegistro = false;

                if(item.glosa.trim().length>4){
                    glosaAnterior = item.glosa;
                }

                item.items.forEach(registro => {
                    recibosSeleccionados.forEach(itemRecibo => {

                        if(itemRecibo.periodo == registro.periodo 
                        && itemRecibo.recibo == registro.recibo){
                            let reciboItem = {};
                            cabeceraRegistro = true;

                            itemsEncontrados.push(registro);

                            reciboItem.numero = registro.recibo;
                            reciboItem.monto = UtilNumerico.formatoMoneda(registro.monto07);

                            this.dataRecibos.push(reciboItem);

                            sumaTotalPagar += registro.monto07;
                        }
                    });
                });

                if(cabeceraRegistro){
                    objItem.codigo = item.codigo;
                    objItem.glosa = item.glosa.trim().length>4 ? item.glosa : glosaAnterior;
                    objItem.gfaFracc = item.gfaFracc;
                    objItem.cabecera = item.cabecera;
                    objItem.items = itemsEncontrados;

                    tributox.push(objItem);
                }
            });
            this.totalPago = sumaTotalPagar;
            return tributox;
        },
        regresarPagina(){
            Util.pagina.go(this, "/pago-tributo/contribuyente/estado-cuenta");
        },
        cancelarProceso(){
            delete this.app.data.cuenta;
            delete this.app.data.siContribuyentesAsociados;

            Util.storage.set(this.app.name, this.app.data);

            Util.pagina.go(this, "/");
        },
        iniciarPago(){
            EventBus.$emit("loadershow", true);

            let codigoCuentaRequest = {};
            codigoCuentaRequest.tdi = this.app.data.contribuyente.seleccionado.tdi;
            codigoCuentaRequest.con = this.app.data.contribuyente.seleccionado.con;
            codigoCuentaRequest.email = this.app.data.contribuyente.email;
            codigoCuentaRequest.nombres = this.app.data.contribuyente.nombres;
            codigoCuentaRequest.direccion = this.app.data.contribuyente.dirfiscal;
            codigoCuentaRequest.recibos = this.dataRecibos;
            codigoCuentaRequest.totalPago = this.totalPago;
            codigoCuentaRequest.emision = this.app.data.cuenta.emision;

            getDataCuenta(codigoCuentaRequest).then((response)=>{
                console.log('pintando respuesta a getdatacuenta')
                console.log(response.data)
                if(response.data.success){
                    let nroPedido = response.data.data.nroOrden;

                    let url = `${Constantes.urlIniciaPagoTributo}/contribuyente/estado-cuenta/seleccionados/pago/${nroPedido}`;
                    window.location.assign(url);
                    location.reload();
                    
                    // EventBus.$emit("loadershow", false);
                }
            });
        }
    },
    filters:{
        formatoMonedax(monto){
            return UtilNumerico.formatoMoneda(monto);
        }
    }
}
</script>


