const util = {};
util.storage = {};
util.pagina = {};
util.alert = {};

// Utilidades para LocalStorage
util.storage.get = (item) => {
    if(localStorage.getItem(item)!=null){
        return JSON.parse(localStorage.getItem(item));
    }else{
        return null;
    }
};

util.storage.set = (item, data) => {
    localStorage.setItem(item, JSON.stringify(data));
};

util.storage.delete = (item) => {
    localStorage.removeItem(item);
};

util.limpiarStorage = () => {
    console.log("Limpiaremos el localStorage");
    localStorage.clear();
};

// Utilidades para alertas
util.alert.success = (entorno, titulo, mensaje) => {
    entorno.$swal({
        toast: true,
        position: "top",
        showConfirmButton: false,
        timer: 1500,
        timerProgressBar: true,
        icon: 'success',
        title: titulo,
        text: mensaje
    });
};

// Otras Utilidades
util.cerrarSesion = (entorno) => {
    console.log("Cerraremos la sesion");
    localStorage.clear();
    entorno.$router.push("/login");
};

util.pagina.go = (entorno, pagina) => {
    entorno.$router.push(pagina);
};

util.pagina.anterior = (entorno) => {
    entorno.$router.go(-1);
};

export default util;


