<template>
    <tbody>
        <tr><td colspan="16">&nbsp;</td></tr>
        <tr><td colspan="16">{{tributo.glosa}}</td></tr>
        <tr>
            <td>&nbsp;</td>
            <td>PERIODO <br/><small>(Año-Periodo)</small></td>
            <td>RECIBO <br/><small>(Número de recibo)</small></td>
            <td>E <br/><small>(Estado)</small></td>
            <td v-for="(itemCabecera, i) in tributo.cabecera" :key="i">
                {{itemCabecera}}
                <br/><small>{{itemCabecera | detalleCabeceraItem}}</small>
            </td>
            <td>TOTAL <br/><small>(Total S/.)</small></td>
            <td>VEN/PAG <br/><small>(Vencimiento/Pago)</small></td>
            <td>COAC <br/><small>(Coactivo)</small></td>
            <td></td>
            <td></td>
            <td><b-icon icon="cart4" font-scale="1.5"></b-icon></td>
        </tr>
        <tr v-for="(item, i) in tributo.items" :key="i">
            <td>
                <h6 v-if="item.periodo.split('-')[1]==='00' && codigotri!=20" class="d-none d-lg-block">
                    <b-badge v-bind:id="item.recibo+''+i" variant="primary">info</b-badge>
                    <b-tooltip v-bind:target="item.recibo+''+i" 
                    :title="item.periodo.split('-')[0]+'-'+tributo.codigo | textoInfoRecibo00"></b-tooltip>
                </h6>

                <div v-if="codigotri==9999" class="d-none d-lg-block">
                    <b-badge v-bind:id="item.recibo+''+i" variant="primary">info</b-badge>
                    <b-tooltip v-bind:target="item.recibo+''+i" 
                    title="Saldo informativo"></b-tooltip>
                </div>
                <div class="h4 mt-0 mb-2 d-lg-none" @click="modalMasInfo(item, tributo.cabecera)">
                    <b-icon icon="info-circle-fill" variant="success"></b-icon>
                </div>
            </td>
            <td>{{item.periodo.trim()}}</td>
            <td>{{item.recibo.trim()}}</td>
            <td>
                <h6>
                    <b-badge :id="item.recibo" variant="info">{{item.estado.trim()}}</b-badge>
                    <b-tooltip :target="item.recibo" :title="item.estado | estadoRecibo"></b-tooltip>
                </h6>
            </td>
            <td>{{item.monto01 | formatoMoneda}}</td>
            <td>{{item.monto02 | formatoMoneda}}</td>
            <td>{{item.monto03 | formatoMoneda}}</td>
            <td>{{item.monto04 | formatoMoneda}}</td>
            <td>{{item.monto05 | formatoMoneda}}</td>
            <td>{{item.monto06 | formatoMoneda}}</td>
            <td>{{item.monto07 | formatoMoneda}}</td>
            <td>{{item.venPag}}</td>
            <td>{{item.coac}}</td>
            <td>{{item.extra}}</td>
            <td>{{item.enc07}}</td>
            <td>
                <div v-if="codigotri!=9999 && origen==='ecuenta'">
                    <div v-if="item.periodo.split('-')[1]==='00'">
                        <b-form-checkbox 
                        v-model="reciboCeroSeleccionado"
                        :value="{periodo:item.periodo , recibo:item.recibo}"
                        unchecked-value="desactivado"
                        :disabled="disabledReciboCeroSeleccionado"
                        @change="changeCheckReciboCero">
                        </b-form-checkbox>
                    </div>
                    <div v-else>
                        <b-form-checkbox
                        v-model="reciboTrimestralSeleccionado"
                        :value="{periodo:item.periodo , recibo:item.recibo}"
                        unchecked-value="desactivado"
                        :disabled="disabledReciboTrimestralSeleccionado"
                        @change="changeCheckReciboTrimestral">
                        </b-form-checkbox>
                    </div>
                </div>
                
            </td>
        </tr>
        <tr>
            <td colspan="4" class="text-right">TOTAL:</td>
            <td class="text-right">{{totalMonto01 | formatoMoneda}}</td>
            <td class="text-right">{{totalMonto02 | formatoMoneda}}</td>
            <td class="text-right">{{totalMonto03 | formatoMoneda}}</td>
            <td class="text-right">{{totalMonto04 | formatoMoneda}}</td>
            <td class="text-right">{{totalMonto05 | formatoMoneda}}</td>
            <td class="text-right">{{totalMonto06 | formatoMoneda}}</td>
            <td class="text-right">{{totalMonto07 | formatoMoneda}}</td>
            <td>&nbsp;</td>
            <td>&nbsp;</td>
            <td>&nbsp;</td>
            <td>&nbsp;</td>
            <td>&nbsp;</td>
        </tr>
    </tbody>
</template>
<script>
import Util from '../../tools/util'
import UtilNumerico from '../../tools/utilNumerico'

export default {
    props:["tributo", "codigotri", "origen", "opcSelTodos", "recibosSeleccionadosTodos"],
    data(){
        return {
            app: {name:"appRentas", data:null},
            totalMonto01: 0,
            totalMonto02: 0,
            totalMonto03: 0,
            totalMonto04: 0,
            totalMonto05: 0,
            totalMonto06: 0,
            totalMonto07: 0,

            reciboCeroSeleccionado: [],
            disabledReciboCeroSeleccionado: false,
            reciboTrimestralSeleccionado: [],
            disabledReciboTrimestralSeleccionado:false,
            classResponsive: "d-none d-sm-block",
            classResponsiveMobil: "d-sm-none",
            origenL:this.origen
        }
    },
    mounted(){
        // var flagLogueado= Util.storage.get('logueadoTramite');
        // if(flagLogueado==false || flagLogueado==null || flagLogueado=='false'){
        //     Util.pagina.go(this, '/login/pago-tributo');
        // } else {
            this.app.data = Util.storage.get(this.app.name);

            this.iniciaProceso();
        // }
    },
    methods:{
        iniciaProceso(){
            
            this.tributo.items.forEach(item => {
                console.log(this.tributo.glosa)
                console.log(this.origenL)
                if(this.origenL=='ecuenta' && item.periodo.split('-')[1]=='00'&& (this.tributo.glosa.indexOf('(001) * IMPUESTO PREDIAL') > -1 ||  this.tributo.glosa.indexOf('ARBITRIOS * ') > -1) ){
                    //&& this.tributo.glosa!='(070) * COSTAS JUDICIALES (PROCESALES)'
                    // this.totalMonto01 = this.totalMonto01 + item.monto01;
                    // this.totalMonto02 = this.totalMonto02 + item.monto02;
                    // this.totalMonto03 = this.totalMonto03 + item.monto03;
                    // this.totalMonto04 = this.totalMonto04 + item.monto04;
                    // this.totalMonto05 = this.totalMonto05 + item.monto05;
                    // this.totalMonto06 = this.totalMonto06 + item.monto06;
                    // this.totalMonto07 = this.totalMonto07 + item.monto07;
                    console.log("entró 1")
                }else if (this.origenL=='ecuentaSeleccionado'){
                    this.totalMonto01 = this.totalMonto01 + (item.monto01x==0?item.monto01:item.monto01x);
                    this.totalMonto02 = this.totalMonto02 + (item.monto02x==0?item.monto02:item.monto02x);
                    this.totalMonto03 = this.totalMonto03 + (item.monto03x==0?item.monto03:item.monto03x);
                    this.totalMonto04 = this.totalMonto04 + (item.monto04x==0?item.monto04:item.monto04x);
                    this.totalMonto05 = this.totalMonto05 + (item.monto05x==0?item.monto05:item.monto05x);
                    this.totalMonto06 = this.totalMonto06 + (item.monto06x==0?item.monto06:item.monto06x);
                    this.totalMonto07 = this.totalMonto07 + (item.monto07x==0?item.monto07:item.monto07x);
                }
                else{
                    this.totalMonto01 = this.totalMonto01 + item.monto01x;
                    this.totalMonto02 = this.totalMonto02 + item.monto02x;
                    this.totalMonto03 = this.totalMonto03 + item.monto03x;
                    this.totalMonto04 = this.totalMonto04 + item.monto04x;
                    this.totalMonto05 = this.totalMonto05 + item.monto05x;
                    this.totalMonto06 = this.totalMonto06 + item.monto06x;
                    this.totalMonto07 = this.totalMonto07 + item.monto07x;
                    console.log("entró 2")
                    console.log("this.totalMonto07->"+ this.totalMonto07)
                }

                if(this.app.data.cuenta.recibossel!=null){
                    if(item.periodo.split('-')[1]=='00'){
                        this.app.data.cuenta.recibossel.forEach(itemRecibo =>{
                            if(itemRecibo.periodo == item.periodo 
                            && itemRecibo.recibo==item.recibo){
                                this.reciboCeroSeleccionado.push(itemRecibo);
                            }
                        });
                    }else{
                        this.app.data.cuenta.recibossel.forEach(itemRecibo =>{
                            if(itemRecibo.periodo == item.periodo 
                            &&itemRecibo.recibo==item.recibo){
                                this.reciboTrimestralSeleccionado.push(itemRecibo);
                            }
                        });
                    }
                }
            });
        },
        changeCheckReciboCero(checked){
            if(checked==null){
                this.$emit("desactiva-pagar-todos", true);
            }
        },
        changeCheckReciboTrimestral(checked){
            if(checked==null){
                this.$emit("desactiva-pagar-todos", true);
            }
        },
        modalMasInfo(item, cabecera){
            let filtros = this.$options.filters;
            let mensaje = "";

            if(item.periodo.split('-')[1]=='00'){
                mensaje = '<div class="col-xs-12 col-md-12 text-center text-success mb-2">'+item.periodo+': Pago anual</div>';
            }

            this.$swal({
                title: '<strong>Detalle del recibo</strong>',
                html:
                    '<div class="container" style="font-size:0.9em;">'+
                    '   <div class="row">'+
                            mensaje +
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>PERIODO:</b></div>'+
                    '         <div>'+item.periodo+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>RECIBO:</b></div>'+
                    '         <div>'+item.recibo+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>ESTADO:</b></div>'+
                    '         <div>'+filtros.estadoRecibo(item.estado)+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>'+cabecera[0].trim()+':</b></div>'+
                    '         <div>'+filtros.formatoMoneda(item.monto01)+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>'+cabecera[1].trim()+':</b></div>'+
                    '         <div>'+filtros.formatoMoneda(item.monto02)+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>'+cabecera[2].trim()+':</b></div>'+
                    '         <div>'+filtros.formatoMoneda(item.monto03)+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>'+cabecera[3].trim()+':</b></div>'+
                    '         <div>'+filtros.formatoMoneda(item.monto04)+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>'+cabecera[4].trim()+':</b></div>'+
                    '         <div>'+filtros.formatoMoneda(item.monto05)+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>'+cabecera[5].trim()+':</b></div>'+
                    '         <div>'+filtros.formatoMoneda(item.monto06)+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>TOTAL:</b></div>'+
                    '         <div>'+filtros.formatoMoneda(item.monto07)+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>VEN/PAG:</b></div>'+
                    '         <div>'+item.venPag+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2" style="border-bottom:1px dashed #CCC;">'+
                    '         <div><b>COAC:</b></div>'+
                    '         <div>'+item.coac+'</div>'+
                    '      </div>'+
                    '      <div class="col-6 col-sm-3 col-md-4 pl-1 pr-1 pb-2 pt-2">'+
                    '         <div><b>FISCA:</b></div>'+
                    '         <div>'+item.extra+'</div>'+
                    '      </div>'+
                    '   </div>'+
                    '</div>',
                showCloseButton: true,
            });
        }
    },
    watch:{
        reciboCeroSeleccionado(valor, anterior){
            if(!this.opcSelTodos){
                if(valor.length==0){
                    this.disabledReciboTrimestralSeleccionado = false;
                    this.$emit("eliminar-recibo", anterior[0]);
                }else{
                    this.disabledReciboTrimestralSeleccionado = true;
                    if(valor.length>anterior.length){
                        console.log('entro a agregar-recibo 281')
                        this.$emit("agregar-recibo", valor[valor.length-1]);
                    }else{
                        let recibo = "";

                        for(let i=0;i<anterior.length;i++){
                            if(valor.indexOf(anterior[i])===-1){
                                recibo = "";
                                recibo = anterior[i];
                                break;
                            }
                        }

                        this.$emit("eliminar-recibo", recibo);
                    }
                }
            }else{
                if(valor.length<=0){
                    this.disabledReciboTrimestralSeleccionado = false;
                }else{
                    this.disabledReciboTrimestralSeleccionado = true;
                }
            }
        },
        reciboTrimestralSeleccionado(valor, anterior){
            if(!this.opcSelTodos){
                if(valor.length==0){
                    this.disabledReciboCeroSeleccionado = false;
                    this.$emit("eliminar-recibo", anterior[0]);
                }else{
                    this.disabledReciboCeroSeleccionado = true;

                    if(valor.length>anterior.length){
                        console.log('entro a agregar-recibo 313')
                        this.$emit("agregar-recibo", valor[valor.length-1]);
                    }else{
                        let recibo = "";

                        for(let i=0;i<anterior.length;i++){
                            if(valor.indexOf(anterior[i])===-1){
                                recibo = "";
                                recibo = anterior[i];
                                break;
                            }
                        }

                        this.$emit("eliminar-recibo", recibo);
                    }
                }
            }else{
                if(valor.length==0){
                    this.disabledReciboCeroSeleccionado = false;
                }else{
                    this.disabledReciboCeroSeleccionado = true;
                }
            }
        },
        recibosSeleccionadosTodos(seleccionado, anterior){
            this.reciboCeroSeleccionado = [];
            this.reciboTrimestralSeleccionado = [];
console.log("leyendo tributos")
console.log(this.tributo)
console.log(seleccionado)
            if(seleccionado){
                for(let i=0;i<this.tributo.items.length;i++){
                    let reciboSeleccionado = {};
                    reciboSeleccionado.periodo = this.tributo.items[i].periodo;
                    reciboSeleccionado.recibo = this.tributo.items[i].recibo;
                    console.log("seleccionado")
                    console.log(reciboSeleccionado.recibo)
                    console.log(this.tributo.items[i])
                    if(this.tributo.items[i].periodo.split('-')[1]=='00'){
                        console.log("entro 1")
                        this.reciboCeroSeleccionado.push(reciboSeleccionado);
                        console.log('entro a agregar-recibo 349')
                        this.$emit("agregar-recibo", reciboSeleccionado);
                        console.log("codigo-->"+this.tributo.codigo+"-"+this.tributo.items[i].estado)
                        if(this.tributo.codigo+"-"+this.tributo.items[i].estado!="20-C"
                            && this.tributo.codigo+"-"+this.tributo.items[i].estado!="30-C"
                            && this.tributo.codigo+"-"+this.tributo.items[i].estado!="40-C"
                            && this.tributo.codigo+"-"+this.tributo.items[i].estado!="50-C"
                            && this.tributo.codigo+"-"+this.tributo.items[i].estado!="60-C"
                            && this.tributo.codigo+"-"+this.tributo.items[i].estado!="70-C"
                            && this.tributo.codigo+"-"+this.tributo.items[i].estado!="80-C"
                            && this.tributo.codigo+"-"+this.tributo.items[i].estado!="90-C"){
                            console.log("entro 2")
                            console.log("entro 2")
                            break;
                        }
                    }else{
                        console.log("entro 3")
                        this.reciboTrimestralSeleccionado.push(reciboSeleccionado);
                        console.log('entro a agregar-recibo 365')
                        this.$emit("agregar-recibo", reciboSeleccionado);
                    }
                }
            }
        }
    },
    filters:{
        formatoMoneda(monto){
           return UtilNumerico.formatoMoneda(monto);
        },
        textoInfoRecibo00(anio){
            let txtTipo = "";
            let tipo = anio.split("-");

            if(tipo[1]<20){                
                return tipo[0]+"-00: Pago anual (IMPUESTO PREDIAL)";
            }else{
                return tipo[0]+"-00: Pago anual (ARBITRIOS)";
            }
        },
        estadoRecibo(estado){
            let txtEstado = "";
            if(estado=="A"){      txtEstado = "Tributo activo"; }
            else if(estado=="N"){ txtEstado = "Notificado"; }
            else if(estado=="C"){ txtEstado = "Coactivo"; }
            else if(estado=="P"){ txtEstado = "Pagado"; }
            else if(estado=="F"){ txtEstado = "Fraccionado"; }
            else if(estado=="D"){ txtEstado = "Descargado"; }
            else if(estado=="Q"){ txtEstado = "Quebrado"; }
            else if(estado=="O"){ txtEstado = "Condonado"; }
            else if(estado=="R"){ txtEstado = "Exceso"; }
            else{                 txtEstado = estado; }

            return txtEstado;
        },
        detalleCabeceraItem(cabeceraItem){
            let detalleCabecera = "";

            if(cabeceraItem.trim()=="INSOLUTO"){
                detalleCabecera = "(Monto sin intereses)";
            }else if(cabeceraItem.trim()=="DER_EMIS"){
                detalleCabecera = "(Derecho emisión)";
            }else if(cabeceraItem.trim()=="INTE/MORA"){
                detalleCabecera = "(Interés/Mora)";
            }else if(cabeceraItem.trim()=="REAJUSTES"){
                detalleCabecera = "(Reajustes)";
            }else if(cabeceraItem.trim()=="OTROS"){
                detalleCabecera = "(otros)";
            }else if(cabeceraItem.trim()=="DESCUENTO"){
                detalleCabecera = "(Descuento)";
            }else if(cabeceraItem.trim()=="LIMPIEZA"){
                detalleCabecera = "(Limpieza pública)";
            }else if(cabeceraItem.trim()=="PARQUES"){
                detalleCabecera = "(Parques y jardines)";
            }else if(cabeceraItem.trim()=="SERENAZGO"){
                detalleCabecera = "(Serenazgo)";
            }else if(cabeceraItem.trim()=="INTE/REAJ"){
                detalleCabecera = "(Inte/Reaj)";
            }else if(cabeceraItem.trim()==""){
                detalleCabecera = "";
            }else{
                detalleCabecera = "("+cabeceraItem+")";
            }

            return detalleCabecera;
        }
    }
}
</script>
<style lang="scss">
.panel-filtros{
    background: #EEEEEE;

    -webkit-box-shadow: 0px 4px 8px -2px rgba(0,0,0,0.31);
    -moz-box-shadow: 0px 4px 8px -2px rgba(0,0,0,0.31);
    box-shadow: 0px 4px 8px -2px rgba(0,0,0,0.31);
}
.tabla-reporte{
    width:100%;
    border-collapse:collapse;
    border: 0px;
    font-size: 0.80em;
    
    tr:nth-child(1){
        border-bottom:0px;
    }
    tfoot tr:last-child{
        border-bottom: 0px;
    }
    td{
        padding: 5px 0px 5px 7px;
    }
    tbody{
        tr{
            border-bottom:1px solid #BABCBE;
        }
        tr:nth-child(2n+1){
            background: #E7EAED;
        }
        tr:nth-child(1){
            background:none;
        }
        tr:nth-child(2){
            font-weight: bold;
        }
        tr:nth-child(3){
            background: #6B92B5 !important;
            color:#FFF;
            font-weight: bold;
            line-height: 15px;
        }

        tr:nth-child(3) td{
            padding: 8px 0px 5px 7px !important;
        }
        
        tr:last-child{
            background: #E7EAED;
            font-weight: bold;
        }
        td:nth-child(5),
        td:nth-child(6),
        td:nth-child(7),
        td:nth-child(8),
        td:nth-child(9),
        td:nth-child(10),
        td:nth-child(11){
            text-align: right;
        }
        td:nth-child(2),
        td:nth-child(3),
        td:nth-child(4),
        td:nth-child(12),
        td:nth-child(13),
        td:nth-child(14){
            text-align: center;
        }
    }
}

@media (max-width: 575px){
    .tabla-reporte tbody td:nth-child(3), 
    .tabla-reporte tbody td:nth-child(5), 
    .tabla-reporte tbody td:nth-child(6), 
    .tabla-reporte tbody td:nth-child(7), 
    .tabla-reporte tbody td:nth-child(8), 
    .tabla-reporte tbody td:nth-child(9), 
    .tabla-reporte tbody td:nth-child(10), 
    .tabla-reporte tbody td:nth-child(13), 
    .tabla-reporte tbody td:nth-child(14), 
    .tabla-reporte tbody td:nth-child(15) {
        display: none;
    }
}
@media (min-width: 576px) and (max-width: 767px){
    .tabla-reporte tbody td:nth-child(5), 
    .tabla-reporte tbody td:nth-child(6), 
    .tabla-reporte tbody td:nth-child(7), 
    .tabla-reporte tbody td:nth-child(8), 
